import './scss/main.scss';
import projects from './data/projects';
import { toggleClass, toggleClassOfElements } from './helpers/dom';

const navigationHeader = document.getElementById('js-nav-header');
const floatingTopLink = document.getElementById('js-floating-link');
const technologyList = document.querySelectorAll('[data-technology]');
const technologySublist = document.querySelectorAll('[data-technology-list]');
const showcaseContent = document.getElementById('js-showcase-content');
const pageOffsets = Array.from(document.querySelectorAll('.page:not(.top-page)'), el => el.offsetTop);

function selectTechnology(e) {
  const { technology } = e.target.dataset;

  if (!technology) {
    return;
  }

  toggleClassOfElements(technologyList, ({ dataset }) => dataset.technology === technology, 'active');

  toggleClassOfElements(technologySublist, ({ dataset }) => dataset.technologyList === technology, 'active');
}

let scrollState = '';

function onScroll() {
  const scrollOffset = document.documentElement.scrollTop;
  const isAtTop = scrollOffset === 0;

  // handles the display of floating arrow bottom right
  toggleClass(floatingTopLink, 'active', !isAtTop);

  // the navbar should hide when the scroll offset equals the a page offset
  const isAtPageBorder = pageOffsets.includes(Math.round(scrollOffset));

  if (
    !isAtTop && !isAtPageBorder && scrollState === 'active' ||
    isAtPageBorder && scrollState === 'border'
  ) {
    return;
  }

  if (toggleClass(navigationHeader, 'hide', isAtPageBorder)) {
    scrollState = 'border';
  } else if (toggleClass(navigationHeader, 'active', !isAtTop && !isAtPageBorder)) {
    scrollState = 'active';
  } else {
    scrollState = 'top';
  }
}

function renderShowcaseData() {
  const transformTitleToGitUrl = title => title.toLowerCase().replace(' ', '-');

  showcaseContent.innerHTML += Object.entries(projects).reduce((html, [key, { image, labels, content }]) => {
    return html + `
      <a class="project" href="https://github.com/sweerstra/${transformTitleToGitUrl(key)}" target="_blank" rel="noopener noreferrer">
        <div class="project__thumbnail ${image.includes('mobile') ? 'is-contained' : 'is-covered'}">
          <img src="${image}" alt="${key}"/>
        </div>
        <div class="project__header">
          <h3 class="project__heading">${key}</h3>
          <div class="project__labels">
            ${labels.map(label =>
              `<span class="project__label">${label}</span>`
            ).join('')}
          </div>
        </div>
        <p class="project__content">
          ${content}
        </p>
      </a>
    `;
  }, '');
}

technologyList.forEach(technology => technology.addEventListener('click', selectTechnology));

window.addEventListener('scroll', onScroll);
window.addEventListener('DOMContentLoaded', () => {
  renderShowcaseData();
  onScroll();
});



